@import url(https://fonts.googleapis.com/css2?family=Quattrocento:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
	font-family: ParadiseThoughts;
	src: url(/portfolio/static/media/ParadiseThoughts-Regular.c0d804eb.otf);
}

/* ------------------------------ colors global ----------- */

:root {
	--pink:#f9c4be;
	--blue:#dfefeb;
	--white:#fef6f0;
	--gray:#66605b;
	--brown:#d1afaa;
}

* {
color: #66605b;
color: var(--gray);
font-family: "Quattrocento";
}

.bg-pink {
    --bs-bg-opacity: 1;
    background-color: #f9c4be !important;
    background-color: var(--pink) !important;
}

.bg-blue {
    --bs-bg-opacity: 1;
    background-color: #dfefeb !important;
    background-color: var(--blue) !important;
}

.bg-white {
    --bs-bg-opacity: 1;
    background-color: #fef6f0 !important;
    background-color: var(--white) !important;
}

.bg-brown {
    --bs-bg-opacity: 1;
    background-color: #d1afaa !important;
    background-color: var(--brown) !important;
}

.text-primary {
    --bs-text-opacity: 1;
    color: #66605b !important;
    color: var(--gray) !important;
}


h1 {
font-family: "Quattrocento";
text-align: center;
}

h2 {
font-family: "ParadiseThoughts";
font-size: calc(3rem + 1.2vw);
text-align: center;
}

.navbar-nav-scroll{
	--bs-scroll-height: 100px;
}

/* ------------------------------ border ----------- */

.border-pink{
border: solid 10px #f9c4be;
border: solid 10px var(--pink);
}

/* ------------------------------ buttons ----------- */


.btn-pink {
    color: #fff;
    background-color: #f9c4be;
    background-color: var(--pink);
    border-color: #f9c4be;
    border-color: var(--pink);
}
.btn-pink:hover {
    color: #f9c4be;
    color: var(--pink);
    background-color: #fef6f0;
    background-color: var(--white);
    border-color: #f9c4be;
    border-color: var(--pink);
}

.btn-blue {
    color: #d1afaa;
    color: var(--brown);
    background-color: #dfefeb;
    background-color: var(--blue);
    border-color: #dfefeb;
    border-color: var(--blue);
}
.btn-blue:hover {
    color: #d1afaa;
    color: var(--brown);
    background-color: #dfefeb;
    background-color: var(--blue);
    border-color: #d1afaa;
    border-color: var(--brown);
}



/* ------------------------------ card ----------- */
.mt-n1 {
  margin-top: -1rem !important;
}

/* ------------------------------ @médias ----------- */

@media screen and (min-width: 1200px) {
	.etoiles {
	background-image: url(data:image/png;base64,);
	background-position: right bottom;
	background-repeat: no-repeat;
	}
}
